<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue"  :isBack="true">
			<block slot="content">座位详情</block>
		</cu-custom>

		<view class="cu-card dynamic" :class="isCard?'no-card':''" v-for="(item,i) in list" :key="i">
			<view class="cu-item shadow">
				<view class="cu-list menu-avatar">
					<view class="cu-item">
						<view class="cu-avatar round lg"
							style="background-image:url(https://ossweb-img.qq.com/images/lol/web201310/skin/big10006.jpg);">
						</view>
						<view class="content flex-sub">
							<view>管理员</view>
							<view class="text-gray text-sm flex justify-between">
								{{item.datetime}}
							</view>
						</view>
					</view>
				</view>
				<view class="text-content">
					{{item.title}}
				</view>
				<view class="grid flex-sub padding-lr" :class="isCard?'col-3 grid-square':'col-1'">
					<view class="bg-img" :class="isCard?'':'only-img'"
						:style="{backgroundImage: 'url('+item.trans_pic +')'}">
					</view>
				</view>

			</view>
		</view>

	</view>
</template>

<script>
	import configData from '@/config/index.js'
	const urllocal = configData.httpUrl
	export default {
		created() {

		},
		onShow() {
			this.userinfoObj = uni.getStorageSync('userinfo')
			this.init()
		},
		data() {
			return {
				userinfoObj: {},
				isCard: false,
				list: []
			}
		},
		methods: {
			async init() {
				let that = this
				let msgobj = {
					userid: this.userinfoObj.id
				}
				let resext = await new this.Request(this.Urls.m().queryfloor, msgobj)
					.modepost()

				that.list = resext.map((val, i) => {
					val.trans_pic = urllocal + val.trans_pic.split('^^^')[0]
					let userid = val.userid
					let flag = true
					if (userid) {
						flag = false
					} else {
						flag = true
					}
					val.isdianzan = flag
					return val
				})
				console.log(resext,that.list)
				
			},
			getnowdate() {
				var nowDate = new Date();
				var year = nowDate.getFullYear();
				var month = nowDate.getMonth() + 1 < 10 ? "0" + (nowDate.getMonth() + 1) : nowDate.getMonth() + 1;
				var day = nowDate.getDate() < 10 ? "0" + nowDate.getDate() : nowDate.getDate();
				var dateStr = year + "-" + month + "-" + day;
				return dateStr;
			},
			getnowtime() {
				let date = new Date()
				let hours = date.getHours()
				let minutes = date.getMinutes() < 10 ? ('0' + date.getMinutes()) : date.getMinutes()
				let seconds = date.getSeconds() < 10 ? ('0' + date.getSeconds()) : date.getSeconds()
				let time = hours + ':' + minutes + ':' + seconds
				return time

			},
			
			onPullDownRefresh() {
				this.init()
				uni.stopPullDownRefresh();
			}
		}
	}
</script>

<style>
	.mainsub {
		padding-left: 5%;
		padding-right: 5%;
		background-color: #fff;
	}
</style>